
<template>
  <div v-if="toy" class="toy-details">
      <h1>TOY DETAILS:</h1>
      <h1>{{toy.name}}</h1>
      <h2>Price: {{toy.price}}</h2>
      <h2>Category: {{toy.type}}</h2>
      <h2>InStock: {{toy.inStock}}</h2>
      <router-link to="/toy">Back to ALL</router-link>
      <router-link :to="'/toy/edit/'+ toy._id">Edit Toy</router-link>
  </div>
</template>

<script>

import toyService from '@/services/ToyService.js'
export default {
  name: 'ToyDetails',
  data() {
    return {
        toy: null
    }
  },
    created() {
        let {toyId} = this.$route.params
        console.log(toyId);
        
        toyService.getToyById(toyId)
            .then(toy => this.toy = toy)
    }
}
</script>
