<template>
  <div class="home flex center">
    <keep-alive>
      <toy-filter :filter="filterBy" @clear="clearFilter" @setFilter="setFilter"/>
    </keep-alive>
    <ToyList @deleteToy="deleteToy" :toys="toys"/>
  </div>
</template>

<script>

import ToyList from '@/components/ToyList.vue'
import ToyDetails from '@/views/ToyDetails.vue'
import ToyFilter from '@/components/ToyFilter.vue'
import ToyCharts from '@/views/ToyCharts.vue'

export default {
  name: 'home',
  components: {
    ToyList,
    ToyDetails,
    ToyFilter,
    ToyCharts
  },
  data() {
    return {
      filterBy: null
    }
  },
  methods: {
    deleteToy(toyId) {
      this.$store.dispatch({ type: 'deleteToy', toyId })
    },
    setFilter(filterBy) {
      this.filterBy = filterBy;
      this.$store.dispatch({ type: 'loadToys', filterBy: this.filterBy })
      },
    clearFilter() {
      this.filterBy = null;
      this.$store.dispatch({ type: 'clearFilter' })
        .then(() => this.$store.dispatch({ type: 'loadToys', filterBy: null }))
    }
  },
  computed: {
      toys() {
            return this.$store.getters.toys;
          },
  },
  created() {
    this.$store.dispatch({ type: 'loadToys', filterBy: this.filterBy })
      .then(() => this.filterBy = this.$store.getters.filter)
  }
}
</script>
