import Vue from 'vue'
import Vuex from 'vuex'
import toyService from './services/ToyService'
import userService from './services/UserService'
Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    toys: null,
    filterBy: null
  },
  mutations: {
    setToys(state, {toys}) {
      state.toys = toys
    },
    updateToy(state, {toy}) {
      const idx = state.toys.findIndex(toyItem => toy._id === toyItem._id)
      state.toys.splice(idx, 1, toy)
    },
    removeToy(state, {toyId}) {
      const idx = state.toys.findIndex(toy => toy._id === toyId)
            state.toys.splice(idx, 1);
    },
    addToy(state, {toy}) {
      state.toys.unshift(toy)
    },
    setFilter(state, {filterBy}) {
      state.filterBy = filterBy;
    },
  },
  getters: {
    toys(state) {
      return state.toys;
    },
    filter(state) {
      return state.filterBy;
    }
  },
  actions: {
    loadToys(context, {filterBy}) {
      if (!filterBy) filterBy = context.state.filterBy;     
      return toyService.query(filterBy)
        .then(toys => {
          context.commit({ type: 'setToys', toys });
          context.commit({type: 'setFilter', filterBy})
        })
    },
    saveToy(context, {toy}) {
      const isEdit = !!toy._id;
      return toyService.saveToy(toy)
        .then((savedToy) => {
            if (isEdit) {
                context.commit({ type: 'updateToy', toy: savedToy });
            }
            else {
              context.commit({ type: 'addToy', toy: savedToy })
            }
    })},
    deleteToy(context, {toyId}) {
      return toyService.removeToy(toyId)
      .then(() => {
          context.commit({ type: 'removeToy', toyId })
      })
    },
    clearFilter(context) {
      context.commit({type: 'setFilter', filterBy: null})
    },
    login(context, {username}) {
      return userService.login(username) 
        .then(() => {
          context.commit({type: 'setUser', username})
        })
    }
  }
})
