import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import ToyDetails from './views/ToyDetails.vue'
import ToyEdit from './views/ToyEdit.vue'
import ToyCharts from './views/ToyCharts.vue'
import Login from './views/Login.vue'
// import ToyList from './components/ToyList.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/toy',
      name: 'Home',
      component: Home
    },
    {
      path: '/chart',
      name: 'ToyCharts',
      component: ToyCharts
    },
    {
      path: '/toy/edit/:toyId?',
      name: 'ToyEdit',
      component: ToyEdit
    },
    {
      path: '/toy/:toyId',
      name: 'ToyDetails',
      component: ToyDetails
    },

  ]
})
