<template>
  <div class="toy-filter flex center">
    <div class="txt-search flex center">
      <div>Search</div>
        <input :class="{filtered : filterBy.txt !== ''}" @keyup="setFilter" type="text" v-model="filterBy.txt" placeholder="Enter name">
      </div>
      <div class="filters flex center">
        <!-- <label>Filter</label> -->
        <div :class="{filtered : filterBy.inStock !== null}">{{isInstock}}</div>
        <input id="checkbox" type="checkbox" v-model="filterBy.inStock">
        <div>Category</div>
        <select v-model="filterBy.type" :class="{filtered : filterBy.type}">
            <option>Funny</option>
            <option>Adult</option>
            <option>Educational</option>
        </select>
        <button @click="setFilter">Filter</button>
        </div>
        <div class="sorts flex center">
          <div >Sort by:</div>
          <div :class="{filtered : filterBy.sort === 'name'}" @click="sort('name')" class="sort-btn">Name</div>
          <div :class="{filtered : filterBy.sort === 'price'}" @click="sort('price')" class="sort-btn">Price</div>
        </div>
    
    <button @click="clear">Clear</button>
  </div>
</template>

<script>

import toyService from '@/services/ToyService.js'
export default {
  name: 'ToyFilter',
  props: ['filter'],
  data() {
    return {
        filterBy: {
            txt: '',
            inStock: null,
            type: null,
            sort: null,
        },
    }
  },
  methods: {
      setFilter() {
          this.$emit('setFilter', this.filterBy)
      },
      sort(by) {
        this.filterBy.sort = (by === 'name')? 'name' : 'price';
        this.setFilter();
      },
      clear() {
        this.filterBy = {
            txt: '',
            inStock: null,
            type: null,
            sort: null,
            }
        this.$emit('clear')
      },
  },
  computed: {
    isInstock() {
      if (this.filterBy.inStock === null) return 'Stock';
      return (this.filterBy.inStock) ? 'In stock' : 'Out of stock'
    },
    stockFiltered() {
      if (this.filterBy.inStock) return filtered
    },
  },
  created() {
    if (this.filter) { 
      let filter = this.filter;
      this.filterBy = JSON.parse(JSON.stringify(filter))
    };
  },
  watch: {
    filter() {
      if (this.filter) { 
      let filter = this.filter;
      this.filterBy = JSON.parse(JSON.stringify(filter))
    }
    }
  }

}
</script>
<style>
    .toy-filter {
      flex-wrap: wrap;
      background-color: lightblue
    }
    .toy-filter >* {
      margin: 7px;
    }
    .sort-btn {
        cursor: pointer;
        font-weight: bold;
        padding: 10px
    }
    .sort-btn:hover {
      color: red;
    }
    .filtered {
      border: 2px solid red
    }
</style>
