<template>
  <div v-if="toy" class="toy-edit">
      <h1>TOY DETAILS:</h1>
      <h1>{{toy.name}}</h1>
      <h2>Price: {{toy.price}}</h2>
      <h2>Category: {{toy.type}}</h2>
      <h2>InStock: {{ toy.inStock ? "Yes" : "No" }}</h2>
      <form @submit.prevent="saveToy">
          <label>Name</label>
        <input type="text" v-model="toy.name">
         <label>Price</label>
        <input type="number" v-model.number="toy.price">
        <label >Type</label>
        <select v-model="toy.type">
            <option>Funny</option>
            <option>Adult</option>
            <option>Educational</option>
        </select>
        <label for="checkbox">In Stock</label>
        <input id="checkbox" type="checkbox" v-model="toy.inStock">
        <button>Save</button>
      </form>
      <router-link to="/toy">Back to All</router-link>
  </div>
</template>

<script>

import toyService from '@/services/ToyService.js'
export default {
  name: 'ToyEdit',
  data() {
    return {
        toy: null,
    }
  },
  methods: {
      saveToy(){
          this.$store.dispatch({type:'saveToy', toy: this.toy})
                .then(()=>{
                    this.$router.push('/toy')
                })
      }
  },
    created() {
        let {toyId} = this.$route.params
        if (toyId) {
        toyService.getToyById(toyId)
            .then(toy => this.toy = toy)
        } else {
            this.toy= toyService.getEmpty()
        }
    }
}
</script>
