/* eslint-disable no-console */
export default {
    query,
    getToyById,
    getEmpty,
    saveToy,
    removeToy
}
import axios from 'axios';

// const BASE_URL = 'http://localhost:3003/toy';
const BASE_URL = ( process.env.NODE_ENV !== 'development')
    ? '/api/toy'
    : '//localhost:3000/api/toy' ;

const resolveData = res => res.data;

function query(filter) {
    if (!filter)  { 
        console.log('no filter');
        return axios.get(`${BASE_URL}`)
            .then(resolveData)}
    else {        
        return axios.get(`${BASE_URL}/?txt=${filter.txt}&&inStock=${filter.inStock}&&type=${filter.type}&&sort=${filter.sort}`)
            .then(resolveData)}
}

function getToyById(toyId) {
    return axios.get(`${BASE_URL}/${toyId}`)
        .then(resolveData)
}
function getEmpty() {
    let at = Date.now().toLocaleString();
    return {
        "name": "",
        "price": 0,
        "type": "",
        "createdAt": at,
        "inStock": true
    }
}
function saveToy(toy) {
    if (toy._id) {
        return axios.put(`${BASE_URL}/${toy.id}`, toy)
            .then(resolveData)
    } else {
        return axios.post(BASE_URL, toy)
            .then(resolveData)
    }
}
function removeToy(id) {
    return axios.delete(`${BASE_URL}/${id}`)
}