<template>
    <section class="login">
        <h1>Welcome!</h1>
        <h2>Please login</h2>
        <form @submit.prevent="login">
            <label>Username</label>
            <input type="text" placeholder="please enter your username" v-model="username">
            <button @click.stop="login">Login</button>
        </form>
    </section>
</template>
<script>
export default {
    data() {
        return {
            username: ''
        }
    },
    methods: {
        login() {
            if(this.username) this.$store.dispatch({ type: 'login', username: this.username })
        }
    }
}
</script>

